.beakers {
  width: 100%;
  height: 100%;
  grid-template-columns: 25% 25% 25% 25%;
  display: grid;
  grid-template-rows: 25% 25% 25% 25%;
  grid-area: 1/1/5/5; }
  .beakers .beaker1 {
    grid-area: 1/3/2/4;
    z-index: 5;
    display: inline-flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 4.5vw; }
  .beakers .beaker2 {
    grid-area: 1/2/2/3;
    z-index: 5;
    display: inline-flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 4.5vw; }
